<template>
  <article class="markdown-section" id="main">
      <div>
          <h1>{{ title }}</h1>
          <item v-for="item in mainItems" v-bind:item="item" :key="item.id"></item>

          <div v-for="(tag, key) in tags" :key="key">
              <h2 :id="tag.tag">{{ tag.title }}</h2>
              <p>{{ tag.desc }}</p>
              <item v-for="item in tagItems[tag.tag]" v-bind:item="item" :key="item.id"></item>
          </div>
      </div>
  </article>
</template>

<script>
    import Item from '@/components/Item.vue'
    
    function filterCategory(item) {
        return item.tags.includes(String(this))
    }
    function inverseFilterCategory(item) {
        return (item.tags.includes(String(this)) ? false : true)
    }

    export default {
      name: 'Guidelines',
      components: {
          Item
      },
      created: function () {
          if (!(this.$route.params.cat in this.$parent.categories)) {
            this.$router.replace({name:'home'});
          }
      },
      computed: {
        title: function () {
          if (this.$parent.categories == null) return null
          return this.$parent.categories[this.$route.params.cat].title
        },
        tags: function () {
          if (this.$parent.categories == null) return null
          return this.$parent.categories[this.$route.params.cat].subcategories
        },
        items: function () {
          if (this.$parent.guidelines == null) return null
          return this.$parent.guidelines.filter(filterCategory, this.$route.params.cat)
        },
        mainItems: function () {
          if (this.items == null) return null
          var items = this.items
          for (var key in this.tags) {
             items = items.filter(inverseFilterCategory, this.tags[key].tag)
          }
          return items
        },
        tagItems: function () {
          if (this.items == null) return {}
          var items = {}
          for (var key in this.tags) {
             items[this.tags[key].tag] = this.items.filter(filterCategory, this.tags[key].tag)
          }
          return items
        },
      }
    }
</script>