<template>
    <div class="item">
        <a class="file" v-on:click="view(item)">
            <span class="version" title="version">{{ item.version }}</span>
            <h3>{{ item.name }}</h3>
        </a>
        <ul class="tags">
            <li v-for="tag in item.tags" :key="tag" :class="tag">{{ tag }}</li>
        </ul>
        <p v-if="item.description != ''">{{ item.description }}</p>
        <span class="source"><a :href="item.source_link">{{ item.source_link }}</a></span>
        <span class="review">{{ item.review_date }}</span>
    </div>
</template>

<script>
    export default {
        name: 'Item',
        props: {
            item: Object
        },
        methods: {
            view: function (item) {
                this.$router.push({name:'viewer', params:{item: item}});
            }
        }
    }
</script>
