<template>
    <div class="viewer" v-if="item">
        <div class="item">
            <a class="file" :href="item.file" target="_blank">
                <span class="version" title="version">{{ item.version }}</span>
                <h3>{{ item.name }}</h3>
            </a>
            <ul class="tags">
                <li v-for="tag in item.tags" :key="tag" :class="tag">{{ tag }}</li>
            </ul>
        </div>

        <div v-if="ext == '.pdf'">
            <embed v-if="pdfViewer()" :src="item.file" class="embed" type="application/pdf">
            <iframe v-else :src="pdfjs" class="embed"></iframe>
        </div>
        <div v-else>
            <iframe :src="item.file" class="embed"></iframe>
        </div>
        
    </div>
  </template>
  
  <script>   
      export default {
        name: 'Viewer',
        props: {
            item: Object
        },
        created: function () {
          if (this.item == null) {
            this.$router.replace({name:'home'});
          }
        },
        computed: {
          ext: function () {
            return this.item.file.substring(this.item.file.length - 4, this.item.file.length);
          },
          pdfjs: function() {
            return "/pdf.js/viewer.html?file=" + this.item.file
          }
        },
        methods: {
            pdfViewer: function () {
              var plugin = navigator.mimeTypes["application/pdf"];
              return (plugin && plugin.enabledPlugin);
            }
        }
      }
  </script>