<template>
    <main>
        <input type="checkbox" id="sidebar-toggle" v-model="sidebar">
        <label for="sidebar-toggle" class="sidebar-toggle">
            <div class="sidebar-toggle-button"><span></span><span></span><span></span></div>
        </label>
        <aside class="sidebar">
            <h1 class="app-name">
                <router-link :to="{ name: 'home' }" class="app-name-link">{{ title }}</router-link>
            </h1>
            <div class="search">
                <div class="input-wrap">
                    <input type="search" v-model="query" placeholder="Search...">
                    <button class="clear-button" v-bind:class="{ show: clear }" aria-label="Clear search" v-on:click="query = ''">
                        <svg width="16" height="16" viewBox="0 0 16 16" xmlns="http://www.w3.org/2000/svg">
                            <circle cx="8" cy="8" r="8" fill="black"></circle>
                            <path stroke="white" stroke-width="1.5" d="M4.5,4.5,11.5,11.5" vector-effect="non-scaling-stroke"></path>
                            <path stroke="white" stroke-width="1.5" d="M4.5,11.5,11.5,4.5" vector-effect="non-scaling-stroke"></path>
                        </svg>
                    </button>
                </div>
                <div class="results-panel show">
                    <p v-if="results == '' && clear" class="empty">No results!</p>
                    <div v-for="(result, key) in results" :key="key" class="matching-post">
                        <a v-on:click="view(result)">
                            <h2>{{ result.name }}</h2>
                            <p>{{ result.description }}</p>
                        </a>
                    </div>
                </div>
            </div>
            <div v-if="!clear" class="sidebar-nav">
                <ul>
                    <router-link v-for="(category, key) in categories" :key="key" :to="{ name: 'category', params: { cat: category.tag }}" tag="li" exact-active-class="active">
                        <a>{{ category.title }}</a>
                        <ul v-if="category.subcategories != null" class="app-sub-sidebar">
                            <router-link v-for="(sCategory, sKey) in category.subcategories" :key="sKey" :to="{ name: 'category', params: { cat: category.tag }, hash: '#' + sCategory.tag }" tag="li" exact-active-class="active"><a>{{ sCategory.title }}</a></router-link>
                        </ul>
                    </router-link>

                    <template v-if="links != null">
                        <li><strong>Links</strong></li>
                        <li v-for="(link, key) in links" :key="key">
                            <a :href="link.href" target="_blank"><img v-if="link.icon" :src="link.icon">{{ link.title }}</a>
                        </li>
                    </template>
                </ul>
            </div>
        </aside>
        <section class="content">
            <router-view v-if="!loading"/>
            <home class="home" v-if="loading"></home>
        </section>
    </main>
</template>

<script>
    import Home from '@/views/Home.vue'
    import Lunr from 'lunr'
    import YAML from 'yaml-js'
    import csvjson from 'csvjson'
    export default{
        name: 'Organisation',
        components: {
          Home
        },
        data () {
            return {
                title: "Guidelines",
                desc: null,
                loading: true,
                categories: null,
                guidelines: null,
                links: null,
                guidelineCsv: null,
                categoriesYaml: null,
                sidebar: false,
                index: null,
                query: null,
                results: null,
                clear: false
            }
        },
        beforeRouteEnter (to, from, next) {
            var xhr = new XMLHttpRequest()
            xhr.open('GET', '/' + to.params.org + '/config.yaml')
            xhr.onload = function () {
                var data = YAML.load(xhr.responseText);
                if (typeof data == 'object') {
                    next(vm => vm.loadConfig(data));
                } else {
                    next({replace: true, name: 'root'});
                }
            }
            xhr.send()
        },
        watch: {
            '$route'() {
                this.sidebar = false
            },
            query: function(val) {
                this.results = this.search(val)
                if (val != "") {
                    this.clear = true
                } else {
                    this.clear = false
                    this.results = ""
                }
            },
            guidelineCsv: function(val) {
                var self = this
                this.fetchCsv(val, function (data){
                    self.guidelines = data
                    self.buildIndex();
                });
            },
            categoriesYaml: function(val) {
                var self = this
                this.fetchYaml(val, function (data){
                    var output = {}
                    for (var category in data) {
                        output[data[category].tag] = data[category];
                    }
                    self.categories = output
                    self.loading = false
                });
            },
            title: function(val) {
                document.title = val;
            }
        },
        /*created: function () {
            var self = this
            this.fetchYaml('/' + this.$route.params.org + '/config.yaml', function (data){
                self.loadConfig(data)
            });
        },*/
        methods: {
            loadConfig: function(data) {
                this.links = data.links
                this.guidelineCsv = data.guidelines
                this.categoriesYaml = data.categories
                this.title = data.title
                this.desc = data.desc
            },
            fetchCsv: function (url, data) {
                var xhr = new XMLHttpRequest()
                xhr.open('GET', url)
                xhr.onload = function () {
                    const output = []
                    var options = {
                        delimiter : ',',
                        quote     : '"'
                    };
                    var records = csvjson.toArray(xhr.responseText, options);
                    for(var key in records){
                        var record = records[key]
                        var tags = record[2].split(",").map(function(item) {
                            return item.trim();
                        });

                        var guideline = {
                            "name": record[1],
                            "tags": tags,
                            "description": record[3],
                            "version": record[4],
                            "review_date": record[5],
                            "file": record[6],
                            "source_link": record[7]
                        }

                        output.push(guideline)
                    }
                    data(output)
                }
                xhr.send()
            },
            fetchYaml: function (url, data) {
                var xhr = new XMLHttpRequest()
                xhr.open('GET', url)
                xhr.onload = function () {
                    data(YAML.load(xhr.responseText))
                }
                xhr.send()
            },
            fetchData: function (url, data) {
                var xhr = new XMLHttpRequest()
                xhr.open('GET', url)
                xhr.onload = function () {
                    data(JSON.parse(xhr.responseText))
                }
                xhr.send()
            },
            buildIndex: function () {
                var self = this
                this.index = Lunr(function () {
                    this.ref('id')
                    this.field('name', { boost: 10 });
                    this.field('tags');
                    this.field('description');

                    for (var key in self.guidelines) {
                        this.add({
                            'id': key,
                            'name': self.guidelines[key].name,
                            'tags': self.guidelines[key].tags,
                            'description': self.guidelines[key].description
                        });
                    } 
                });
            },
            search: function (term) {
                var results = this.index.search(term);
                var items = []
                if (results.length) {
                    for (var i = 0; i < results.length; i++) {
                        var item = this.guidelines[results[i].ref];
                        items.push(item);
                    }
                }
                return items
            },
            view: function (item) {
                this.$router.replace({name:'home'});
                this.$router.push({name:'viewer', params:{item: item}});
            }
        }
    }
</script>